import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Swiper, SwiperSlide } from 'swiper/react';
import { IMG_DOMEN } from '../app/config';
import { RootState } from '../app/store';
import { Pagination } from 'swiper';
import 'swiper/css';
import 'swiper/css/pagination';

export const ChatGallery = () => {
    const { gallery: imageList } = useSelector(
        (state: RootState) => state.chat
    );
    const [isSwiperActive, setIsSwiperActive] = useState(false);
    const [activeSlideIdx, setActiveSlideIdx] = useState(0);

    return (
        <>
            {!isSwiperActive && (
                <div
                    className="row gy-2 mt-2"
                    style={{ maxHeight: '80vh', overflow: 'auto' }}
                >
                    {imageList.map((image, idx) => (
                        <div className="col-6 col-sm-2 col-md-3" key={image.id}>
                            <div
                                className="square-image border"
                                role="button"
                                onClick={() => {
                                    setIsSwiperActive(true);
                                    setActiveSlideIdx(idx);
                                }}
                            >
                                <img
                                    loading="lazy"
                                    src={IMG_DOMEN + image.src}
                                    alt={image.alt}
                                />
                            </div>
                        </div>
                    ))}
                    {imageList.length ? (
                        ''
                    ) : (
                        <h2 className="text-center">No images</h2>
                    )}
                </div>
            )}
            {isSwiperActive && (
                <div
                    className="position-absolute top-0 start-0 w-100 h-100"
                    style={{ zIndex: 1, backgroundColor: 'rgba(0, 0, 0, .9)' }}
                    onClick={e => {
                        if (e.target === e.currentTarget)
                            setIsSwiperActive(false);
                    }}
                >
                    <div
                        className="container position-relative h-100"
                        style={{ paddingTop: '36px' }}
                        onClick={e => {
                            if (e.target === e.currentTarget)
                                setIsSwiperActive(false);
                        }}
                    >
                        <div
                            className="position-absolute top-0 end-0"
                            style={{ margin: '6px' }}
                        >
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="24"
                                height="24"
                                fill="#fff"
                                className="bi bi-file-arrow-down me-1 me-sm-2"
                                viewBox="0 0 16 16"
                                role="button"
                            >
                                <path d="M8 5a.5.5 0 0 1 .5.5v3.793l1.146-1.147a.5.5 0 0 1 .708.708l-2 2a.5.5 0 0 1-.708 0l-2-2a.5.5 0 1 1 .708-.708L7.5 9.293V5.5A.5.5 0 0 1 8 5z" />
                                <path d="M4 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h8a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H4zm0 1h8a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H4a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1z" />
                            </svg>
                            <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="24"
                                height="24"
                                fill="#fff"
                                className="bi bi-x-square"
                                viewBox="0 0 16 16"
                                role="button"
                                onClick={e => {
                                    if (e.target === e.currentTarget)
                                        setIsSwiperActive(false);
                                }}
                            >
                                <path d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z" />
                                <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                            </svg>
                        </div>
                        <Swiper
                            spaceBetween={50}
                            onSlideChange={() => console.log('slide change')}
                            onSwiper={swiper =>
                                swiper.slideTo(activeSlideIdx, 0)
                            }
                            modules={[Pagination]}
                            pagination={{
                                clickable: true,
                                type: 'fraction',
                            }}
                            className="h-100"
                        >
                            {imageList.map(image => (
                                <SwiperSlide
                                    key={image.id}
                                    className="d-flex justify-content-center align-items-center"
                                    onClick={e => {
                                        if (e.target === e.currentTarget)
                                            setIsSwiperActive(false);
                                    }}
                                >
                                    <img
                                        className="mw-100"
                                        loading="lazy"
                                        src={IMG_DOMEN + image.src}
                                        alt={image.alt}
                                    />
                                </SwiperSlide>
                            ))}
                        </Swiper>
                    </div>
                </div>
            )}
        </>
    );
};
