import { getUser } from './../user/userSlice';
import { getNewToken } from './../auth/authSlice';
import { RootState } from './../../app/store';
import { ICHat } from '../../common/models/index';
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import axios from 'axios';
import { setIsLoading } from '../user/userSlice';

export interface IChatListState {
    chatList: ICHat[];
    activeIndex: number;
}

interface GetChatListArgs {
    profileId: string | undefined;
}

const initialState: IChatListState = {
    activeIndex: 0,
    chatList: [],
};

export const chatListSlice = createSlice({
    name: 'chatList',
    initialState,
    reducers: {
        setChatList: (state, action: PayloadAction<ICHat[]>) => {
            state.chatList = action.payload;
        },
        setActiveIndex: (state, action: PayloadAction<number>) => {
            state.activeIndex = action.payload;
        },
    },
});

// Action creators are generated for each case reducer function
export const { setChatList, setActiveIndex } = chatListSlice.actions;

export const chatListReducer = chatListSlice.reducer;

//thunk functions
export const getChatList = createAsyncThunk(
    'chatList/getChatList',
    async ({ profileId }: GetChatListArgs, thunkAPI) => {
        const { token } = (thunkAPI.getState() as RootState).auth;
        thunkAPI.dispatch(setIsLoading(true));
        axios
            .get('https://dialog.api.dc2.a4sex.net/api/contact_intimates', {
                params: {
                    userId: profileId,
                },
                headers: {
                    Authorization: 'Bearer ' + token,
                },
            })
            .then(res => {
                thunkAPI.dispatch(
                    setChatList(
                        (res.data as ICHat[]).filter(
                            message => message.dialog !== null
                        )
                    )
                );
                thunkAPI.dispatch(setIsLoading(false));
            })
            .catch(async error => {
                if (error.response) {
                    if (error.response.status === 401) {
                        await thunkAPI.dispatch(getNewToken());
                        thunkAPI.dispatch(getChatList({ profileId }));
                    } else console.log(error.response);
                }
            });
        await thunkAPI.dispatch(getUser(profileId));
    }
);
