import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import { setActiveIndex } from '../features/chatList/chatListSlice';
import { UserProfile } from './UserProfile';
import htmlParser from 'html-react-parser';

interface ChatListProps {
    profileId?: string | undefined;
}

export const ChatList = ({ profileId }: ChatListProps) => {
    const dispatch = useDispatch();
    const { isLoading } = useSelector((state: RootState) => state.user);
    const { chatList } = useSelector((state: RootState) => state.chatList);
    const navigate = useNavigate();

    const chatListMarkup = chatList.map((chat, idx) => (
        <div
            className="border p-2 mb-2 d-flex align-items-center flex-column flex-sm-row"
            key={chat.id}
            onClick={() => {
                if (profileId) dispatch(setActiveIndex(idx));
                navigate(
                    profileId ? `./chat/${chat.human.id}` : `/${chat.human.id}`
                );
            }}
            role="button"
        >
            <div className="flex-shrink-0 me-sm-2 ">
                <b className="me-2">{chat.human.name ?? 'Unknown'}</b>
                <span className="text-danger">
                    {chat.human.age ?? 'unknown'}
                </span>
            </div>
            <div className="flex-fill text-sm-end text-break">
                {htmlParser(chat.dialog.message)}
            </div>
        </div>
    ));
    return (
        <div className="row justify-content-center">
            <div className="col-sm-10 col-md-8 col-lg-6 mb-2">
                {!isLoading && (
                    <>
                        {profileId && (
                            <UserProfile
                                userId={profileId}
                                className="mb-2"
                                showTags={true}
                            />
                        )}
                        {chatList.length ? (
                            chatListMarkup
                        ) : (
                            <h2 className="text-center">No chats found..</h2>
                        )}
                    </>
                )}
            </div>
        </div>
    );
};
