import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import { RootState } from '../app/store';
import { getChatList } from '../features/chatList/chatListSlice';
import { reserUser } from '../features/user/userSlice';

interface SearchProfileProps {
    className?: string;
    profileId: string | undefined;
}

export const SearchProfile = ({ className, profileId }: SearchProfileProps) => {
    const dispatch = useDispatch();
    const { isLoading } = useSelector((state: RootState) => state.user);
    const [profileIdInput, setProfileIdInput] = useState(profileId ?? '');

    useEffect(() => {
        setProfileIdInput(profileId ?? '');
        dispatch(getChatList({ profileId }) as any);
        return () => {
            dispatch(reserUser());
        };
    }, [dispatch, profileId]);

    const handleChangeProfileId = (e: React.FormEvent<HTMLInputElement>) => {
        const value = e.currentTarget.value;
        if (Number.isNaN(+value)) return;
        setProfileIdInput(value);
    };

    return (
        <div
            className={`row flex-column flex-sm-row justify-content-center align-items-center ${className}`}
        >
            <div className="col-8 col-sm-4 mb-2">
                <Link className="btn btn-primary btn-sm w-100" to={'/saved/'}>
                    Saved Profiles
                </Link>
            </div>
            <div className="d-flex justify-content-center align-items-center">
                <label htmlFor="inputUserId" className="h1">
                    Search profile
                </label>
            </div>
            <div className="col-8 col-sm-5 col-md-4 col-lg-3 col-xl-2 mb-2 mb-sm-0">
                <div className="input-group">
                    <input
                        type="text"
                        id="inputUserId"
                        className="form-control text-center"
                        aria-describedby="passwordHelpInline"
                        placeholder="Type profile id"
                        value={profileIdInput}
                        onChange={handleChangeProfileId}
                        disabled={isLoading}
                    />
                    <span className="input-group-text position-relative">
                        <Link to="/" className="stretched-link" />
                        <svg
                            xmlns="http://www.w3.org/2000/svg"
                            width="16"
                            height="16"
                            fill="currentColor"
                            className="bi bi-x"
                            viewBox="0 0 16 16"
                        >
                            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z" />
                        </svg>
                    </span>
                </div>
            </div>
            <div className="col-8 col-sm-auto">
                <Link
                    className={
                        'btn btn-primary w-100' + (isLoading ? ' disabled' : '')
                    }
                    to={`/${profileIdInput}`}
                >
                    Search
                    {isLoading && (
                        <span
                            className="spinner-border spinner-border-sm ms-2"
                            role="status"
                            aria-hidden="true"
                        />
                    )}
                </Link>
            </div>
        </div>
    );
};
