import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import { removeSavedUser } from '../features/user/userSlice';

export const SavedUsers = () => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const { savedUsers } = useSelector((state: RootState) => state.user);

    return (
        <>
            <div className="row justify-content-center">
                <div className="col-8 col-sm-4 mb-2">
                    <div
                        className="btn btn-secondary btn-sm w-100"
                        onClick={() => {
                            navigate(-1);
                        }}
                    >
                        Back
                    </div>
                </div>
            </div>
            <h1 className="text-center">Saved users</h1>
            {!savedUsers.length && (
                <h2 className="text-center">No users saved yet</h2>
            )}
            {savedUsers.map(user => (
                <div
                    key={user.id}
                    className="border mb-2 p-2 d-flex justify-content-between align-items-center position-relative"
                >
                    <div>
                        <b className="me-2">{user.name ?? 'Unknown'}</b>
                        <span className="text-danger">
                            {user.age ?? 'unknown'}
                        </span>
                        <Link to={`/${user.id}`} className="stretched-link" />
                    </div>
                    <div
                        className="btn btn-primary"
                        style={{ zIndex: 1 }}
                        onClick={() => {
                            dispatch(removeSavedUser(user.id));
                        }}
                    >
                        Delete
                    </div>
                </div>
            ))}
        </>
    );
};
