import { useDispatch, useSelector } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { RootState } from '../app/store';
import { setActiveIndex } from '../features/chatList/chatListSlice';
import { UserProfile } from './UserProfile';

interface ChatControlsProps {
    profileId: string | undefined;
    interlocutorId: string | undefined;
    isGalleryOpen: boolean;
    setIsGalleryOpen: React.Dispatch<React.SetStateAction<boolean>>;
    isAutoscrollActive: boolean;
    setIsAutoscrollActive: React.Dispatch<React.SetStateAction<boolean>>;
}

export const ChatControls = ({
    profileId,
    interlocutorId,
    isGalleryOpen,
    setIsGalleryOpen,
    isAutoscrollActive,
    setIsAutoscrollActive,
}: ChatControlsProps) => {
    const dispatch = useDispatch();
    const { activeIndex, chatList } = useSelector(
        (state: RootState) => state.chatList
    );
    const { gallery: imageList } = useSelector(
        (state: RootState) => state.chat
    );
    const { isLoading } = useSelector((state: RootState) => state.user);
    const navigate = useNavigate();

    return (
        <div className="row justify-content-center position-relative">
            <div className="d-flex justify-content-between align-items-center mb-2 position-relative">
                <div
                    className={
                        'btn btn-outline-primary my-1' +
                        (activeIndex - 1 < 0 ? ' disabled opacity-0' : '')
                    }
                    onClick={() => {
                        setIsAutoscrollActive(false);
                        dispatch(setActiveIndex(activeIndex - 1));
                        navigate(
                            `/${profileId}/chat/${
                                chatList[activeIndex - 1].human.id
                            }`,
                            {
                                replace: true,
                            }
                        );
                    }}
                >
                    
                </div>
                {isLoading ? (
                    <h3 className="position-relative mb-0">
                        Loading: {interlocutorId}
                        <Link
                            to={`/${interlocutorId}`}
                            className="stretched-link"
                            replace={true}
                        />
                    </h3>
                ) : (
                    <UserProfile userId={interlocutorId} />
                )}
                <div
                    className={
                        'btn btn-outline-primary my-1' +
                        (activeIndex + 1 > chatList.length - 1
                            ? ' disabled opacity-0'
                            : '')
                    }
                    onClick={() => {
                        setIsAutoscrollActive(false);
                        dispatch(setActiveIndex(activeIndex + 1));
                        navigate(
                            `/${profileId}/chat/${
                                chatList[activeIndex + 1].human.id
                            }`,
                            {
                                replace: true,
                            }
                        );
                    }}
                >
                    
                </div>
            </div>
            <div className="text-center">
                <div
                    className="btn btn-secondary me-2"
                    onClick={() => {
                        navigate(-1);
                    }}
                >
                    Back
                </div>
                <div
                    className={
                        'btn btn-primary me-2' + (isLoading ? ' disabled' : '')
                    }
                    onClick={() => {
                        setIsGalleryOpen(!isGalleryOpen);
                    }}
                >
                    {isGalleryOpen ? 'Close gallery' : 'Gallery'}
                    {` (${imageList.length})`}
                </div>
                <div
                    className={
                        'btn ' +
                        (isAutoscrollActive ? 'btn-primary' : 'btn-secondary') +
                        (isLoading ? ' disabled' : '')
                    }
                    onClick={() => {
                        setIsAutoscrollActive(!isAutoscrollActive);
                    }}
                >
                    AutoScroll
                </div>
            </div>
        </div>
    );
};
