import { RootState } from './../../app/store';
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import axios from 'axios';

export interface IUser {
    id: number;
    name: string;
    age: string;
    lastOnline: number;
    status: string;
    shortStatus: string;
    tags: ITag[];
    isSaved?: boolean;
}

interface ITag {
    id: number;
    tag: string;
}

export interface IChatListState {
    user: IUser;
    savedUsers: IUser[];
    isLoading: boolean;
}

const initialState: IChatListState = {
    user: {
        id: 0,
        name: '',
        age: '',
        lastOnline: 0,
        status: '',
        shortStatus: '',
        tags: [],
    },
    savedUsers: JSON.parse(localStorage.getItem('savedProfiles') ?? '[]'),
    isLoading: true,
};

export const userSlice = createSlice({
    name: 'user',
    initialState,
    reducers: {
        setUser: (state, action: PayloadAction<IUser>) => {
            state.user = action.payload;

            const isSavedUser = state.savedUsers.some(
                user => user.id === state.user.id
            );
            state.user.isSaved = isSavedUser;
        },
        addSavedUser: (state, action: PayloadAction<IUser>) => {
            state.savedUsers.unshift(action.payload);
            state.user.isSaved = true;
            localStorage.setItem(
                'savedProfiles',
                JSON.stringify(state.savedUsers)
            );
        },
        removeSavedUser: (state, action: PayloadAction<number>) => {
            state.savedUsers = state.savedUsers.filter(
                user => user.id !== action.payload
            );
            state.user.isSaved = false;
            localStorage.setItem(
                'savedProfiles',
                JSON.stringify(state.savedUsers)
            );
        },
        reserUser: state => {
            state.user = initialState.user;
        },
        setIsLoading: (state, action: PayloadAction<boolean>) => {
            state.isLoading = action.payload;
        },
    },
});

// Action creators are generated for each case reducer function
export const {
    setIsLoading,
    setUser,
    reserUser,
    addSavedUser,
    removeSavedUser,
} = userSlice.actions;

export const userReducer = userSlice.reducer;

//thunk functions
export const getUser = createAsyncThunk(
    'user/getUser',
    async (profileId: string | undefined, thunkAPI) => {
        if (!profileId) return;
        const { token } = (thunkAPI.getState() as RootState).auth;
        axios
            .get('https://search.api.dc2.a4sex.net/api/users/' + profileId, {
                headers: {
                    Authorization: 'Bearer ' + token,
                },
            })
            .then(res => {
                const lastOnline = res.data.last;
                let lastOnlineString = `${lastOnline} seconds`;
                if (lastOnline > 60) {
                    // minutes
                    if (lastOnline > 60 * 60) {
                        // days
                        if (lastOnline > 60 * 60 * 24) {
                            lastOnlineString = `${(
                                lastOnline /
                                60 /
                                60 /
                                24
                            ).toFixed(2)} days`;
                        } else {
                            // hours
                            lastOnlineString = `${(
                                lastOnline /
                                60 /
                                60
                            ).toFixed(2)} hours`;
                        }
                    } else
                        lastOnlineString = `${(lastOnline / 60).toFixed(
                            2
                        )} minutes`;
                }
                thunkAPI.dispatch(
                    setUser({
                        id: res.data.id,
                        name: res.data.name ?? 'Unknown',
                        age: res.data.age ?? 'unknown',
                        lastOnline,
                        status: lastOnlineString,
                        shortStatus: res.data.last < 600 ? 'Online' : 'Offline',
                        tags: res.data.tags,
                    })
                );
            })
            .catch(error => {
                if (error.response) {
                    if (error.response.status === 401) {
                        thunkAPI.dispatch(getUser());
                    } else console.log(error.response);
                }
            });
    }
);
