import { getUser } from './../user/userSlice';
import { getNewToken } from './../auth/authSlice';
import { RootState } from './../../app/store';
import { IMessage, IImage } from './../../common/models/index';
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import type { PayloadAction } from '@reduxjs/toolkit';
import axios from 'axios';
import { setIsLoading } from '../user/userSlice';

export interface IChatState {
    chatMessages: IMessage[];
    gallery: IImage[];
}

interface GetChatMessagesArgs {
    profileId: string | undefined;
    interlocutorId: string | undefined;
}

const initialState: IChatState = {
    chatMessages: [],
    gallery: [],
};

export const chatSlice = createSlice({
    name: 'chat',
    initialState,
    reducers: {
        addChatMessages: (state, action: PayloadAction<IMessage[]>) => {
            state.chatMessages.push(...action.payload);
        },
        addImages: (state, action: PayloadAction<IImage[]>) => {
            state.gallery.push(...action.payload);
        },
        clearChatMessages: state => {
            state.chatMessages = [];
            state.gallery = [];
        },
    },
});

// Action creators are generated for each case reducer function
export const { addChatMessages, clearChatMessages, addImages } =
    chatSlice.actions;

export const chatReducer = chatSlice.reducer;

const requestChatMessages = async (
    profileId: string | undefined,
    interlocutorId: string | undefined,
    offset: number,
    token: string,
    lastMessageId?: number
) => {
    const res = await axios.get(
        `https://dialog.api.dc2.a4sex.net/api/v1/users/${profileId}/dialog`,
        {
            params: {
                tid: interlocutorId,
                offset,
            },
            headers: {
                Authorization: `Bearer ${token}`,
            },
        }
    );
    const processMessages = async (data: IMessage[]) => {
        const messages: IMessage[] = [];
        const images: IImage[] = [];
        for (const message of data) {
            const [prefix, alias] = message.message
                .replace(/^\[\[|\]\]$/g, '')
                .split(':');
            if (prefix !== 'IMG') {
                message.isImg = false;
                messages.push({ ...message, isImg: false });
                continue;
            }
            try {
                const res = await axios.get(
                    `https://photo.api.dc2.a4sex.net/sends/${alias}`,
                    {
                        params: {
                            tid: message.fromId,
                        },
                        headers: {
                            Authorization: `Bearer ${token}`,
                        },
                    }
                );
                const photo = res.data.photo;
                const src: string = photo._links.origin.href;
                messages.push({ ...message, isImg: true, src });
                images.push({
                    id: photo.id as string,
                    src,
                    alt: photo.copy,
                });
            } catch (error) {
                messages.push({
                    ...message,
                    isImg: false,
                    message: '[[Image not found]]',
                });
            }
        }
        return { messages, images };
    };

    if (lastMessageId) {
        let i = 0;
        while (res.data[i].id !== lastMessageId) {
            console.log(i, res.data[i].id !== lastMessageId);
            i++;
        }
        return await processMessages(res.data.slice(0, i).reverse());
    }

    return await processMessages(res.data);
};

export const updateChatMessages = createAsyncThunk(
    'chat/updateChatMessages',
    async ({ profileId, interlocutorId }: GetChatMessagesArgs, thunkAPI) => {
        const { token } = (thunkAPI.getState() as RootState).auth;
        const { chatMessages } = (thunkAPI.getState() as RootState).chat;
        const { isLoading } = (thunkAPI.getState() as RootState).user;

        if (isLoading) return;
        try {
            if (!chatMessages.length) return;
            const res = await requestChatMessages(
                profileId,
                interlocutorId,
                0,
                token,
                chatMessages[chatMessages.length - 1].id
            );
            if (res.messages.length > 0) {
                thunkAPI.dispatch(addChatMessages(res.messages));
                thunkAPI.dispatch(addImages(res.images));
            }
        } catch (error: any) {
            if (error.response) {
                if (error.response.status === 401) {
                    await thunkAPI.dispatch(getNewToken());
                    thunkAPI.dispatch(
                        updateChatMessages({ profileId, interlocutorId })
                    );
                } else {
                    console.log(error.response);
                }
            }
        }
    }
);

export const getAllChatMessages = createAsyncThunk(
    'chat/getAllChatMessages',
    async ({ profileId, interlocutorId }: GetChatMessagesArgs, thunkAPI) => {
        let offset = 0;
        thunkAPI.dispatch(clearChatMessages());
        thunkAPI.dispatch(getUser(interlocutorId));
        interface IData {
            messages: IMessage[];
            images: IImage[];
            add: Function;
            dispatch: Function;
        }
        const data: IData = {
            messages: [],
            images: [],
            add: (_data: IData) => {
                data.messages.push(..._data.messages);
                data.images.push(..._data.images);
            },
            dispatch: () => {
                thunkAPI.dispatch(addChatMessages(data.messages.reverse()));
                thunkAPI.dispatch(addImages(data.images.reverse()));
                thunkAPI.dispatch(setIsLoading(false));
            },
        };

        const { chatList, activeIndex } = (thunkAPI.getState() as RootState)
            .chatList;
        const primaryChatId = chatList[activeIndex]?.id ?? 0;
        thunkAPI.dispatch(setIsLoading(true));
        while (true) {
            const { token } = (thunkAPI.getState() as RootState).auth;
            const { chatList, activeIndex } = (thunkAPI.getState() as RootState)
                .chatList;
            const curChatId = chatList[activeIndex]?.id ?? 0;
            try {
                const res = await requestChatMessages(
                    profileId,
                    interlocutorId,
                    offset,
                    token
                );

                if (curChatId !== primaryChatId) break;
                data.add(res);

                if (res.messages.length < 15) {
                    data.dispatch();
                    break;
                }
                offset += 15;
            } catch (error: any) {
                if (error.response) {
                    if (error.response.status === 401) {
                        await thunkAPI.dispatch(getNewToken());
                    } else {
                        console.log(error.response);
                        offset += 15;
                    }
                }
            }
        }
    }
);
